body {
  min-height: 100vh;
  background: url("https://source.unsplash.com/nDqA4d5NL0k/2000x2000") 0 0 / cover;
  align-items: center;
  margin: 0;
  font-family: sans-serif;
  display: flex;
}

#bands {
  list-style: inside square;
  width: 500px;
  background: #fff;
  margin: auto;
  padding: 0;
  font-size: 20px;
  box-shadow: 0 0 0 20px rgba(0, 0, 0, .05);
}

#bands li {
  border-bottom: 1px solid #efefef;
  padding: 20px;
}

#bands li:last-child {
  border-bottom: 0;
}

a {
  color: #ffc600;
  text-decoration: none;
}

